import { useState } from 'react';
import { useValidator } from '@validator.tool/hook';
import { isObjectEmpty } from '../utils/is';
import { cloneDeep } from '../utils';
export default function useForm() {
    const [state, setState] = useState({
        initialValues: {},
        store: {},
    });
    const { validator, forceUpdate } = useValidator({
        initValues: state.initialValues,
    });
    const updateStore = (datas) => {
        setState({
            ...state,
            ...datas,
        });
    };
    const innerValidate = () => {
        const { showMessages } = validator;
        showMessages?.();
        forceUpdate?.();
    };
    // 
    const getFieldValue = (field) => {
        const { store } = state;
        return store[field];
    };
    // 
    const setFieldValue = (field, value) => {
        updateStore({
            store: {
                ...state.store,
                [field]: value,
            },
        });
    };
    // 
    const resetFieldValue = () => {
        updateStore({
            store: { ...state.initialValues },
        });
    };
    // 
    const validate = () => {
        const { showMessages, errorMessages } = validator;
        showMessages?.();
        forceUpdate?.();
        if (Object.keys(errorMessages).length > 0) {
            return errorMessages;
        }
        return {};
    };
    // 
    const validateFields = () => {
        return new Promise(async function (resolve, reject) {
            const errors = validate();
            if (isObjectEmpty(errors)) {
                const value = cloneDeep(state.store);
                resolve(value);
            }
            else {
                reject(errors);
            }
        });
    };
    const getFormInstance = () => {
        return {
            getFieldValue,
            setFieldValue,
            resetFieldValue,
            validate,
            validateFields,
            getInnerMethods: (inner) => {
                let methods = {};
                if (inner) {
                    methods = {
                        store: state.store,
                        initialValues: state.initialValues,
                        updateStore,
                        validator,
                        forceUpdate,
                        innerValidate,
                    };
                }
                return methods;
            },
        };
    };
    return getFormInstance();
}
